
























import Vue from "vue";
export default Vue.extend({
  data: () => ({
    fee: "",
  }),
  props: {
    dialog: {
      required: true,
      type: Boolean,
    },
    row: {
      required: true,
      type: Number,
    },
  },
  watch: {
    row: {
      immediate: true,
      handler(val) {
        if (!this.fee) this.fee = val;
      },
    },
  },
  methods: {
    setFee() {
      this.$store.dispatch("admin/setFee", this.fee).then(() => this.closeMe());
    },
    closeMe() {
      this.$emit("closeMe", "isEditFee");
    },
  },
});
