















import Vue from "vue";
export default Vue.extend({
  data: () => ({
    pin: undefined as Record<string, string> | undefined,
  }),
  props: {
    dialog: {
      required: true,
      type: Boolean,
    },
    row: {
      required: true,
      type: Object,
    },
  },
  methods: {
    setPin() {
      this.$store.dispatch("setPin", this.row).then(() => this.closeMe());
    },
    closeMe() {
      this.$emit("closeMe", "isEditPin");
    },
  },
});
