



























import Vue from "vue";
export default Vue.extend({
  data: () => ({
    pin: undefined as Record<string, string> | undefined,
  }),
  props: {
    dialog: {
      required: true,
      type: Boolean,
    },
    row: {
      required: true,
      type: Object,
    },
  },
  methods: {
    remove() {
      if (confirm("حذف العنصر؟")) {
        this.$store
          .dispatch("admin/deleteUnit", this.row.key)
          .then(() => this.closeMe());
      }
    },
    setUnit() {
      this.$store
        .dispatch("admin/setUnit", this.row)
        .then(() => this.closeMe());
    },
    closeMe() {
      this.$emit("closeMe", "isEditUnit");
    },
  },
});
